.project-technologies {
    min-height: 50px;
}
.project-technologies span {
    border-radius: 4px;
    border: 2px solid #f76c6c;
    padding: 7px;
    margin-right: 10px;
    font-size: .85em;
}

.br-tan {
    border-bottom: 2px solid #f8e9a1;
}

