    border-bottom: 2px solid #f76c6c;
}

.br-cyan {
    border-bottom: 2px solid #a8d0e6;
}

.br-blue {
    border-bottom: 2px solid #374785 !important;
}

.br-indigo {
    border-bottom: 2px solid #24305e;
}

.c-tan {
